import { useEffect, useState } from "react"
import { getGifs } from "../helpers/gifsHelper";

export const useFetchGifs = (category) => {
    const [state, setState] = useState({
        data: [],
        loading: true
    });

    useEffect(() => {
        getGifs(category).then( gifs => {

            setState({
                data: gifs,
                loading: false
            });
        });
    }, [ category ]);

    return state;
}